"use client";

import React from "react";
import Link from "next/link";
import basketballimg from "../public/basketball.svg"
import { Button } from "@/components/ui/button";
import { Input } from "@/components/ui/input";
import { Search, Bell, Menu } from "lucide-react";
import {
  Command,
  CommandGroup,
  CommandItem,
  CommandList,
} from "@/components/ui/command";
import {
  Popover,
  PopoverContent,
  PopoverTrigger,
} from "@/components/ui/popover";
import { Badge } from "@/components/ui/badge";
import { Avatar, AvatarImage, AvatarFallback } from "@/components/ui/avatar";
import {
  Collapsible,
  CollapsibleContent,
  CollapsibleTrigger,
} from "@/components/ui/collapsible";
import { Separator } from "@/components/ui/separator";
import Image from "next/image";

const navLinks = ["Dashboard", "Markets", "Products", "Customization"];
const links = ["Your profile", "Settings", "Logout"];

export const Navbar = () => {
  const [isOpen, setIsOpen] = React.useState(false);

  return (
    <>
    <nav className="w-full bg-gray-400 h-[1.5rem] flex justify-between">
      <div className="logo">
        <Image src={""} alt=""/>
      </div>

    </nav>
    <div className="h-screen">
      <Collapsible open={isOpen} onOpenChange={setIsOpen} className="w-full">
        <div className="bg-gray-800 px-2 md:px-5 xl:px-10 2xl:px-20 py-3 mx-auto">
          <div className="flex justify-between items-center">
            <div className="flex items-center space-x-6">
              <Link href="#">
                <svg
                  xmlns="http://www.w3.org/2000/svg"
                  viewBox="0 0 24 24"
                  fill="none"
                  stroke="white"
                  strokeWidth="2"
                  strokeLinecap="round"
                  strokeLinejoin="round"
                  className="h-8 w-8"
                >
                  <path d="M15 6v12a3 3 0 1 0 3-3H6a3 3 0 1 0 3 3V6a3 3 0 1 0-3 3h12a3 3 0 1 0-3-3" />
                </svg>
              </Link>

              <nav className="hidden lg:flex items-center space-x-2 lg:space-x-5">
                {navLinks?.map((link, i) => (
                  <Link href="#" className="" key={i}>
                    <Button
                      variant={link === "Dashboard" ? "default" : "ghost"}
                      className={`${
                        link === "Dashboard"
                          ? "text-white"
                          : "text-gray-200 hover:bg-gray-700 hover:text-white"
                      }`}
                    >
                      {link}
                    </Button>
                  </Link>
                ))}
              </nav>
            </div>

            <div className="lg:hidden w-[270px] sm:w-[410px] md:w-[550px]">
              <form>
                <div className="relative">
                  <Search className="absolute left-2 top-2.5 h-4 w-4 text-muted-foreground" />
                  <Input
                    placeholder="Search"
                    className="pl-8 bg-gray-700 outline-none border-none focus-visible:ring-none"
                  />
                </div>
              </form>
            </div>
            <div className="hidden lg:block">
              <div className="flex items-center space-x-5">
                <div className="">
                  <form>
                    <div className="relative">
                      <Search className="absolute left-2 top-2.5 h-4 w-4 text-muted-foreground" />
                      <Input
                        placeholder="Search"
                        className="pl-8 bg-gray-700 outline-none border-none focus-visible:ring-none"
                      />
                    </div>
                  </form>
                </div>
                <div className="relative">
                  <Bell className="h-7 w-7 text-muted-foreground" />
                  <Badge className="absolute top-0 right-0 w-3 h-3 p-0 text-xs justify-center rounded-xl bg-red-500">
                    4
                  </Badge>
                </div>
                <Popover>
                  <PopoverTrigger asChild>
                    <Avatar className="w-9 h-9 mr-2">
                      <AvatarImage src="https://randomuser.me/api/portraits/men/32.jpg" />
                      <AvatarFallback>J</AvatarFallback>
                    </Avatar>
                  </PopoverTrigger>
                  <PopoverContent className="p-0" align="end">
                    <Command>
                      <CommandList>
                        <CommandGroup>
                          {links.map((link,i) => (
                            <CommandItem className="teamaspace-y-1 px-4 py-2" key={i}>
                              <p>{link}</p>
                            </CommandItem>
                          ))}
                        </CommandGroup>
                      </CommandList>
                    </Command>
                  </PopoverContent>
                </Popover>
              </div>
            </div>

            <CollapsibleTrigger asChild>
              <Button
                onClick={() => {}}
                variant="ghost"
                size="icon"
                className="shadow-none lg:hidden hover:bg-transparent"
              >
                <Menu className="h-10 w-10 text-white" />
              </Button>
            </CollapsibleTrigger>
          </div>
        </div>
        <CollapsibleContent className="lg:hidden bg-gray-800 py-4">
          <div>
            <nav className="flex flex-col space-y-2 px-2 md:px-4">
              {navLinks?.map((link, _) => (
                <Link
                  href="#"
                  className={`w-full font-medium p-2 ${
                    link === "Dashboard"
                      ? "text-white bg-gray-900"
                      : "text-gray-200 hover:bg-gray-700 hover:text-white"
                  }`}
                  key={_}
                >
                  {link}
                </Link>
              ))}
            </nav>
            <Separator className="my-2" />

            <div className="px-2 md:px-4 py-2 flex items-center justify-between">
              <div className="flex items-center space-x-4">
                <Avatar>
                  <AvatarImage
                    src="https://github.com/shadcn.png"
                    alt="@shadcn"
                  />
                  <AvatarFallback>CN</AvatarFallback>
                </Avatar>
                <div className="text-white">
                  <h4 className="font-medium">John Doe</h4>
                  <p className="text-sm">johndoe@example.com</p>
                </div>
              </div>
              <div className="relative">
                <Bell className="h-7 w-7 text-muted-foreground" />
                <Badge className="absolute top-0 right-0 w-3 h-3 p-0 text-xs justify-center rounded-xl bg-red-500">
                  4
                </Badge>
              </div>
            </div>

            <nav className="mt-2 flex flex-col space-y-2 px-2 md:px-4">
              {links?.map((link, i) => (
                <Link
                  href="#"
                  className="w-full font-medium p-2 text-gray-200 hover:bg-gray-700 hover:text-white"
                key={i}>
                  {link}
                </Link>
              ))}
            </nav>
          </div>
        </CollapsibleContent>
      </Collapsible>
    </div>
    </>
  );
};
